<style lang="less" scoped>
div{
  width: 100%;
  background-color: #fff;
  min-height: 100vh;
  text-align: center;
  &>img{
    margin-top: 200px;
    width: 500px;
    height: auto;
  }
  &>p{
    width: 250px;
    height: 70px;
    border-radius: 40px;
    color: #fff;
    text-align: center;
    line-height: 70px;
    background-color: #44b336;
    margin: 0 auto;
    margin-top: 40px;
    font-size: 33px;
  }
}
</style>

<template>
  <div>
    <img src="../../assets/img/404.png" alt="404">
    <p @click="goToDapeng">去大鹏首页</p>
  </div>
</template>
<script>
  export default {
    name: 'error',
    methods: {
      goToDapeng(){
        this.$router.push('/')
      }
    }
  }
</script>


